.information {
  border-top: 1px solid var(--grey-200);
  padding: 20px 0 0;

  &__icon {
    transform: rotate(0deg);

    &--active {
      transform: rotate(90deg);
    }
  }


  &__content {
    display: flex;

    &--active {
      display: none;
    }
  }
}

.day {
  &_sale {
    margin: 0 0 5px;
    font-weight: 600;
    font-size: var(--fs-16);
    text-align: justify;
    text-transform: uppercase;
    color: var(--grey-300);
  }

  &__datetime,
  &__lots {
    font-weight: 400;
    font-size: var(--fs-16);
    text-align: justify;
    color: var(--black-200);
  }
}
