:root {
  /* ------------------- */
  /* font-family         */
  /* ------------------- */
  --ff-primary: "Gilroy", sans-serif;

  /* ------------------- */
  /* container-main      */
  /* ------------------- */
  --content-width: 1230px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  /* ------------------- */
  /* color theme         */
  /* ------------------- */
  --white-100: #fff;
  --black-100: #000;
  --black-200: #171717;
  --grey-100: #f8f8f8;
  --grey-200: #dbdbdb;
  --grey-300: #9c9c9c;
  --grey-400: #6d6d6d;
  --dark-500: #4b4b4b;
  --accent-clr-100: #4f7b9e;
  --accent-clr-200: #ff3030;

  /* ------------------- */
  /* font-size           */
  /* ------------------- */
  --fs-10: 0.625rem;
  --fs-12: 0.75rem;
  --fs-14: 0.875rem;
  --fs-16: 1rem;
  --fs-18: 1.125rem;
  --fs-19: 1.1875rem;
  --fs-21: 1.3125rem;
  --fs-27: 1.6875rem;
  --fs-31: 1.9375rem;
  --fs-41: 2.5625rem;

  /* ------------------- */
  /* transition          */
  /* ------------------- */
  --trs-35: 0.35s ease-in-out;
}
