.static-pagination {
  position: absolute;
  left: 50%;
  bottom: 37px;
  z-index: 9;
  transform: translateX(-50%);
  gap: 8px;

  &__item {
    border-radius: 10rem;
    width: 10px;
    height: 10px;
    background-color: rgba(255, 255, 255, 0.5);
  }
}

.hero-pagination .swiper-pagination-bullet {
  position: relative;
  width: 10px;
  height: 10px;
  background-color: var(--white-100);
  opacity: 1;
  transition: transform var(--trs-35);
}

.hero-pagination .swiper-pagination-bullet-active {
  opacity: 1;
  transform: translateY(55px);

  &::after {
    content: "";
    position: absolute;
    left: 0;
    top: -55px;
    z-index: 100;
    border-radius: 10rem;
    width: 10px;
    height: 10px;
    background-color: var(--accent-clr-100);
    opacity: 1;
    transition: transform var(--trs-35), opacity var(--trs-35);
  }
}

.swiper-pagination.hero-pagination.swiper-pagination-clickable.swiper-pagination-bullets.swiper-pagination-horizontal {
  bottom: 30px;
}

