.hero-slide {
  &__picture {
    position: relative;
    display: block;
    width: 100%;
    height: 500px;

    &::before {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      background-color: rgba(0, 0, 0, 0.3);
    }

    @include tablet {
      height: 400px;
    }
  }

  &__image {
    display: block;
    height: 100%;
    object-fit: cover;
    object-position: center center;
  }

  &__content {
    position: absolute;
    top: 50%;
    max-width: 541px;
    transform: translateY(-50%);
  }

  &__text {
    margin: 0 0 40px;
    padding: 0;
    font-weight: 400;
    font-size: var(--fs-31);
    text-transform: uppercase;
    color: var(--white-100);
  }

  &__btn {
    @include mobile {
      display: none;
    }
  }
}
