.page-product {
  margin: 40px 0 0;

  &__content {
    @include tablet {
      flex-direction: column;
    }
  }

  &__item {
    &:nth-child(2n + 1) {
      width: 100%;
      max-width: 700px;

      @include tablet {
        width: 100%;
        max-width: 100%;
      }
    }

    &:nth-child(2n + 2) {
      width: 100%;
      max-width: 480px;

      @include tablet {
        width: 100%;
        max-width: 100%;
      }
    }
  }

  &__picture {
    height: 600px;
    max-width: 700px;

    @include tablet {
      height: inherit;
      max-width: 100%;
    }
  }

  &__img {
    @include tablet {
      width: 100%;
    }
  }

  &__detail {
    border-top: 1px solid #dbdbdb;
    padding: 20px 0 0;
  }

  &__info-wrapper {
    border-top: 1px solid #dbdbdb;
    padding: 20px 0 0;
  }

  &__title-h2 {
    text-transform: capitalize;
  }
}

.conditions {
  font-weight: 400;
  font-size: 16px;
  line-height: 125%;
  text-align: justify;
  color: #4b4b4b;

  &__link {
    font-weight: 400;
    font-size: var(--fs-16);
    line-height: 1.3;
    text-align: justify;
    text-decoration: underline;
    color: var(--dark-500);
    text-decoration-skip-ink: none;
  }
}

.literature {
  font-weight: 400;
  font-size: var(--fs-16);
  line-height: 1.3;
  text-align: justify;
  color: var(--dark-500);

  &__author {
    font-weight: 600;
    font-size: var(--fs-16);
    line-height: 1.3;
    text-align: justify;
    color: var(--dark-500);
  }
}

.info-product {
  &__title-h3 {
    text-transform: inherit;
  }

  &__detail {
    font-weight: 400;
    font-size: var(--fs-16);
    line-height: 1.3;
    color: var(--grey-400);
  }

  &__price,
  &__current {
    font-weight: 400;
    font-size: var(--fs-21);
    line-height: 1.3;
    text-transform: uppercase;
    color: var(--black-200);
  }
}

.cataloguing {
  font-weight: 400;
  font-size: var(--fs-16);
  line-height: 1.3;
  text-decoration: underline;
  text-transform: uppercase;
  color: var(--accent-clr-100);
  text-decoration-skip-ink: none;
}

.description {
  &__item {
    p {
      font-weight: 400;
      font-size: var(--fs-16);
      line-height: 1.3;
      color: var(--dark-500);

      &:not(:last-child) {
        margin-bottom: 20px;
      }
    }
  }

  &__title-h2 {
    font-style: italic;
    text-align: center;
  }
}
