.pagination {
  &__btn {
    font-weight: 600;
    font-size: var(--fs-16);
    text-transform: uppercase;
    color: var(--grey-300);
    stroke: var(--grey-300);

    &--active {
      color: var(--black-200);
      stroke: var(--black-200);
    }
  }

  &__page {
    align-items: center;
    border: 1px solid var(--grey-300);
    padding: 2px 0 0;
    width: 30px;
    height: 30px;
    font-weight: 600;
    font-size: var(--fs-16);
    color: var(--grey-300);

    &--active {
      border: 1px solid var(--black-100);
      color: var(--black-100);
    }
  }

  @include mobile {
    margin: 0 auto;
  }
}
