.search {
  stroke: var(--black-200);

  &--color {
    stroke: var(--grey-200);
  }

  &__link {
    &:focus {
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      height: 100%;
    }
  }

  &__label {
    width: inherit;
  }

  &__input {
    width: inherit;
    font-weight: 400;
    font-size: var(--fs-14);
    color: var(--black-200);

    &::placeholder {
      color: var(--grey-300);
    }
  }


}
