*,
*::before,
*::after {
  box-sizing: border-box;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

.page {
  // scroll-behavior: smooth;
  height: 100%;
  box-sizing: border-box;
  font-family: var(--ff-primary, sans-serif);
  font-size: var(--fs-16);
  line-height: 1.4;
  -webkit-text-size-adjust: 100%;
  color: var(--black-100);
  background-color: var(--white-100);
  scrollbar-gutter: stable;
}

.page__body {
  margin: 0;
  min-width: 360px;
  min-height: 100%;
  font-size: 16px;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.stop-scroll {
  overflow: hidden;
  width: 100%;
  height: 100dvh;
  overscroll-behavior: none;
}

.site-container {
  display: flex;
  flex-direction: column;
  overflow: hidden; // если используете на сайте position: sticky - уберите эту настройку
  margin: 0 auto;
  min-height: 100dvh;
}

.is-hidden {
  display: none !important;
  /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.ul-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.in-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: var(--white-100);

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.centered {
  text-align: center;
}

.dis-scroll {
  // position: fixed;
  // left: 0;
  // top: 0;
  overflow: hidden;
  width: 100%;
  height: 100dvh;
  overscroll-behavior: none;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.page--ios .dis-scroll {
  position: relative;
}

/* ------------------- */
/* Контейнеры          */
/* ------------------- */
.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  width: 100%;
  max-width: var(--container-width);

  &--large {
    padding: 0;
    max-width: 1600px;
  }
}

.container-full {
  padding: 0 var(--container-offset);
  max-width: 100%;
}

.container-right {
  padding-left: calc((100% - var(--content-width)) / 2 + var(--container-offset));
}

.container-left {
  padding-right: calc((100% - var(--content-width)) / 2 + var(--container-offset));
}

.container-left-50 {
  padding: 0 var(--container-offset);
  max-width: 50%;
}

.container-right-50 {
  margin-left: auto;
  padding: 0 var(--container-offset);
  max-width: 50%;
}

.container-half-left {
  padding-right: calc((100% - var(--content-width)) / 2 + (var(--container-width) / 2));
  padding-left: calc((100% - var(--content-width)) / 2 + var(--container-offset));
}

.container-half-right {
  padding-right: calc((100% - var(--content-width)) / 2 + var(--container-offset));
  padding-left: calc((100% - var(--content-width)) / 2 + (var(--container-width) / 2));
}

.title-h1 {
  margin: 0;
  padding: 0;
  font-weight: 600;
  font-size: var(--fs-41);
  color: var(--black-200);
}

.title-h2 {
  margin: 0;
  padding: 0;
  font-weight: 400;
  font-size: var(--fs-31);
  text-transform: uppercase;
  color: var(--black-200);
}

.title-h3 {
  margin: 0;
  padding: 0;
  font-weight: 400;
  font-size: var(--fs-21);
  text-transform: uppercase;
  color: var(--black-200);
}

.title-h4 {
  margin: 0;
  padding: 0;
  font-weight: 400;
  font-size: var(--fs-18);
  color: var(--black-200);
}

p {
  margin: 0;
  padding: 0;
}
