.checkbox {
  position: relative;

  &__input {
    display: flex;
    border: 1px solid var(--black-200);
    width: 20px;
    height: 20px;
    cursor: pointer;
  }

  &__checkmark {
    position: absolute;
    left: 50%;
    top: 50%;
    display: none;
    transform: translate(-50%, -50%);
    cursor: pointer;
  }
}
