.lot-info {
  margin: 50px 0;

  &__content {
    border-top: 1px solid var(--grey-200);
    padding: 50px 0 0;

    @include tablet {
      flex-direction: column;
    }
  }

  &__picture {
    width: 100%;
    max-width: 479px;

    @include tablet {
      max-width: 100%;
    }
  }

  &__img {
    @include tablet {
      width: 100%;
    }
  }

  &__info {
    width: 100%;
    max-width: 701px;
    text-align: center;

    @include tablet {
      max-width: 100%;
    }
  }

  &__top {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: var(--fs-21);
    color: var(--grey-300);
  }

  &__desc {
    font-weight: 300;
    font-style: italic;
    font-size: var(--fs-31);
    color: var(--black-200);
  }
}
