.header {
  position: relative;
  padding: 17px 0;
  height: 60px;
  background-color: var(--white-100);
  border-bottom: 1px solid var(--grey-200);

  &__count {
    right: -3px;
    top: 0;
    align-items: center;
    border-radius: 10rem;
    padding-top: 2px;
    width: 15px;
    height: 15px;
    font-weight: 700;
    font-size: var(--fs-10);
    line-height: 10px;
    color: var(--white-100);
    background-color: var(--accent-clr-100);
  }

  &__login {
    @include small-tablet {
      display: none;
    }
  }

  &__search {
    @include small-tablet {
      display: none;
    }
  }
}

.nav {
  &__list {}

  &__link {
    font-weight: 600;
    font-size: var(--fs-12);
    text-transform: uppercase;
    color: var(--black-100);
    transition: color var(--trs-35);

    @include hover {
      color: var(--accent-clr-100);
    }
  }

  @include tablet {
    display: none;
  }
}

.login {
  font-weight: 600;
  font-size: var(--fs-12);
  text-transform: uppercase;
  color: var(--black-100);
  stroke: var(--black-100);
  transition: color var(--trs-35), stroke var(--trs-35);

  @include hover {
    color: var(--accent-clr-100);
    stroke: var(--accent-clr-100);
  }
}

.favourite {
  &__link {}

  @include small-tablet {
    display: none;
  }
}

.shop {
  &__link {}
}

.burger {
  display: none;
  align-items: flex-end;
  justify-content: space-between;
  padding: 5px 0;
  width: 24px;
  height: 24px;

  &__line {
    display: inline-block;
    border-radius: 5px;
    width: 16px;
    height: 2px;
    background-color: rgb(0, 0, 0);

    &:nth-child(3n + 3) {
      width: 10px;
    }
  }

  @include tablet {
    display: flex;
  }
}
