.search-dropdown {
  left: 0;
  top: 55px;
  z-index: 3;
  display: none;
  width: 100%;
  background-color: rgba(255, 255, 255, 0.8);
  -webkit-backdrop-filter: saturate(180%) blur(10px);
  backdrop-filter: saturate(180%) blur(10px);

  &--active {
    display: block;
  }

  &__label {
    display: block;
    width: 100%;
  }

  &__input {
    padding: 0 30px;
    width: 100%;
    height: 60px;
    font-weight: 700;
    font-size: var(--fs-16);
    color: var(--black-200);
    background-color: transparent;

    &::placeholder {
      font-weight: 400;
      color: var(--black-200);
    }

    &:focus {
      outline: 0;
    }
  }

  &__btn {
    right: 24px;
    top: 50%;
    stroke: var(--black-200);
    transform: translateY(-41%);
  }
}
