.contacts {
  padding: 30px;
  background-color: var(--grey-100);

  &__tel,
  &__mail {
    font-weight: 400;
    font-size: var(--fs-16);
    text-align: justify;
    color: var(--black-200);
    transition: color var(--trs-35);

    @include hover {
      color: var(--accent-clr-100);
    }
  }

  &__text {
    display: block;
    font-weight: 600;
    font-size: var(--fs-16);
    text-align: justify;
    color: var(--black-200);
  }
}
