.btn {
  border: 1px solid var(--black-100);
  padding: 0 45px;
  height: 60px;
  min-width: 240px;
  font-weight: 400;
  font-size: var(--fs-18);
  color: var(--white-100);
  background-color: var(--black-100);
  transition: color var(--trs-35), background-color var(--trs-35);

  @include hover {
    color: var(--black-100);
    background-color: var(--white-100);
  }

  &--mid {
    width: 100%;
    height: 48px;
    font-size: var(--fs-16);
    color: var(--black-100);
    background-color: transparent;
    transition: color var(--trs-35), background-color var(--trs-35);

    @include hover {
      color: var(--white-100);
      background-color: var(--black-100);
    }
  }
}
