.filter {
  &--collapsed {
    gap: 0;
  }

  &--collapsed .chivron-down {
    transform: rotate(90deg);
  }

  &--collapsed .filter__list {
    display: none;
  }

  &__top {
    span {
      font-weight: 400;
      font-size: 14px;
      text-decoration: underline;
      text-transform: uppercase;
      color: var(--black-200);
      text-decoration-skip-ink: none;
    }
  }

  &__list {
    overflow: hidden;
    border-top: 1px solid var(--grey-200);
    width: 100%;
    max-width: 355px;

    &--collapsed {
      display: none;
    }

    &--collapsed .chivron-down {
      transform: rotate(-180deg);
    }
  }

  &__item {
    border-bottom: 1px solid var(--grey-200);
    padding: 10px 0 0;
    min-height: 46px;
    font-weight: 400;
    font-size: var(--fs-21);
    text-transform: uppercase;
    color: var(--black-200);

    &:last-child {
      border-bottom: none;
    }

    &--active .filter__dropdown {
      display: flex;
    }

    &--active .chivron-down {
      transform: rotate(180deg);
    }
  }

  &__chapter {
    cursor: pointer;
  }

  &__dropdown {
    display: none;
    margin: 0 0 20px;
  }

  &__dropdown-item {
    height: 20px;
  }

  &__dropdown-link {
    width: 100%;
    font-weight: 400;
    font-size: var(--fs-12);
    text-transform: uppercase;
    color: var(--black-200);
  }

  &__dropdown-value {
    font-weight: 400;
    font-size: var(--fs-12);
    text-transform: uppercase;
    color: var(--black-200);
  }
}
