/* stylelint-disable */

@mixin for-desktop {
  @media (min-width: (1025px)) {
    @content;
  }
}

@mixin big-desktop {
  @media (max-width: (1439.98px)) {
    @content;
  }
}

@mixin tablet {
  @media (max-width: (1099.98px)) {
    @content;
  }
}

@mixin small-tablet {
  @media (max-width: (767.98px)) {
    @content;
  }
}

@mixin mobile {
  @media (max-width: (575.98px)) {
    @content;
  }
}

@mixin small-mobile {
  @media (max-width: (475.98px)) {
    @content;
  }
}
