.auction {
  &__content {
    margin: 30px 0 0;
    border-top: 1px solid var(--grey-200);
    padding: 30px 0 0;
    width: 100%;
    max-width: 825px;

    @include tablet {
      max-width: 100%;
    }
  }

  &__container {
    @include tablet {
      flex-direction: column-reverse;
    }
  }
}

.date {
  font-weight: 400;
  font-size: var(--fs-16);
  text-transform: uppercase;
  color: var(--black-200);

  time {
    color: var(--accent-clr-100);
  }

  &__text {
    span {
      padding-top: 3px;
    }
  }
}
