.footer {
  margin: auto 0 0;
  border-top: 1px solid var(--grey-200);
  padding: 20px 0;

  &__list {
    @include small-tablet {
      columns: 2;
      display: block;
    }
  }

  &__item {

    @include small-tablet {
      margin-bottom: 20px;
    }

    &:nth-child(4n + 1) {
      width: 100%;
      max-width: 118px;
    }

    &:nth-child(4n + 2) {
      width: 100%;
      max-width: 152px;
    }

    &:nth-child(4n + 3) {
      width: 100%;
      max-width: 167px;
    }

    &:nth-child(4n + 4) {
      width: 100%;
      max-width: 100px;
    }
  }

  &__header {
    display: block;
    margin: 0 0 20px;
    font-weight: 600;
    font-size: var(--fs-14);
    text-transform: uppercase;
    color: var(--black-200);
  }

  &__link {
    display: block;
    font-weight: 400;
    font-size: var(--fs-12);
    text-transform: uppercase;
    color: var(--black-200);
    transition: color var(--trs-35);

    @include hover {
      color: var(--accent-clr-100);
    }

    &:not(:last-child) {
      margin: 0 0 10px;
    }
  }
}
