.mobile-menu {
  right: -100%;
  top: 60px;
  z-index: 10;
  width: 50%;
  height: 100dvh;
  background-color: rgb(255, 255, 255);
  transition: right var(--trs-35), width var(--trs-35);

  @include small-tablet {
    width: 70%;
  }

  @include mobile {
    width: 80%;
  }

  @include small-mobile {
    width: 90%;
  }

  &__content {
    justify-content: space-between;
    overflow: auto;
    padding: 0 0 60px;
    height: 100%;
    box-sizing: border-box;
  }

  &__list {
    padding: 30px;
  }

  &__item {
    display: flex;
    padding: 20px 0;

    &:not(:last-child) {
      border-bottom: 1px solid var(--grey-200);
    }

    &:nth-child(6n + 1) {
      padding-top: 0;
    }
  }

  &__link {
    font-weight: 600;
    font-size: var(--fs-12);
    text-transform: uppercase;
    color: var(--black-200);
  }

  &__logo {
    justify-content: space-between;
    width: 100%;
  }

  &--active {
    right: 0;
  }
}
