.lots {
  &__item {
    position: relative;

    @include small-tablet {
      grid-column: 6 span;
    }

    @include mobile {
      grid-column: 12 span;
    }
  }

  &__link {
    position: absolute;
    left: 0;
    top: 0;
    z-index: 1;
    width: 100%;
    height: 100%;
  }
}

.lot {
  &__images {
    z-index: 2;
    cursor: pointer;

    @include hover {
      .zoom-icon {
        opacity: 1;
        visibility: visible;
      }

      .lot__img {
        scale: 1.2;
      }
    }
  }

  &__picture {
    overflow: hidden;

    @include tablet {
      width: 100%;
    }
  }

  &__img {
    transition: scale var(--trs-35);
    scale: 1;

    @include tablet {
      width: 100%;
    }
  }
}

.amount {
  font-weight: 400;
  font-size: var(--fs-16);
  text-transform: uppercase;
  color: var(--black-200);

  &__sku {
    color: var(--accent-clr-100);
  }
}

.price {
  &__info {
    font-weight: 400;
    font-size: var(--fs-16);
    text-transform: uppercase;
    color: var(--black-200);
  }

  &__current {
    color: var(--accent-clr-100);
  }
}

.zoom-icon {
  right: 16px;
  top: 16px;
  z-index: 3;
  align-items: center;
  border-radius: 10em;
  width: 40px;
  height: 40px;
  stroke: var(--black-200);
  background-color: var(--white-100);
  opacity: 0;
  visibility: hidden;
  transition: opacity var(--trs-35), visibility var(--trs-35), background-color var(--trs-35), stroke var(--trs-35);

  @include hover {
    stroke: var(--white-100);
    background-color: var(--black-200);
  }
}
