.aside-search {
  &__link {
    width: 100%;
  }

  &__label {
    width: 100%;
  }

  &__icon-search {
    left: 16px;
    top: 50%;
    transform: translateY(-50%);
  }

  &__input {
    outline: 1px solid var(--black-100);
    padding: 0 16px 0 52px;
    width: 100%;
    height: 48px;
    caret-color: var(--black-200);

    &:user-invalid {
      outline-color: var(--accent-clr-200);
    }
  }
}
