/* ------------------- */
/* grid                */
/* ------------------- */

.grid {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
}

.col_2 {
  grid-column: 2 span;
}

.col_3 {
  grid-column: 3 span;
}

.col_4 {
  grid-column: 4 span;
}

.col_5 {
  grid-column: 5 span;
}

.col_6 {
  grid-column: 6 span;
}

.col-span-2 {
  grid-column: span 2 / span 2;
}

.col-span-3 {
  grid-column: span 3 / span 3;
}

.col-span-4 {
  grid-column: span 4 / span 4;
}

.col-span-5 {
  grid-column: span 5 / span 5;
}

.col-span-6 {
  grid-column: span 6 / span 6;
}

/* ------------------- */
/* flex                */
/* ------------------- */

.in-flex {
  display: inline-flex;
}

.flex {
  display: flex;
}

.flex-line {
  display: flex;
  align-items: baseline;
}

.flex-start {
  display: flex;
  align-items: start;
}

.flex-center {
  display: flex;
  align-items: center;
}

.flex-end {
  display: flex;
  align-items: flex-end;
}

.flex-ctr-just {
  display: flex;
  align-items: center;
  justify-content: center;
}

.flex-col {
  display: flex;
  flex-direction: column;
}

.flex-col-just {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

/* ------------------- */
/* justify             */
/* ------------------- */

.just-between {
  justify-content: space-between;
}

.just-ctr {
  justify-content: center;
}

.just-end {
  justify-content: flex-end;
}

.just-start {
  justify-content: flex-start;
}

/* ------------------- */
/* text                */
/* ------------------- */

.txt-center {
  text-align: center;
}

/* ------------------- */
/* block               */
/* ------------------- */

.block {
  display: block;
}

.in-block {
  display: inline-block;
}

/* ------------------- */
/* gap                 */
/* ------------------- */

.gap5 {
  gap: 0.3125rem;
}

.gap10 {
  gap: 0.625rem;
}

.gap15 {
  gap: 0.9375rem;
}

.gap20 {
  gap: 1.25rem;
}

.gap25 {
  gap: 1.5625rem;
}

.gap30 {
  gap: 1.875rem;
}

.gap35 {
  gap: 2.1875rem;
}

.gap40 {
  gap: 2.5rem;
}

.gap45 {
  gap: 2.8125rem;
}

.gap50 {
  gap: 3.125rem;
}

/* ------------------- */
/* nargin              */
/* ------------------- */

.mrg-l-auto {
  margin-left: auto;
}

.mrg-r-auto {
  margin-right: auto;
}

.mrg-c-auto {
  margin-inline: auto;
}

.mrg10 {
  margin-inline: 0.625rem;
}

.mrg20 {
  margin-inline: 1.25rem;
}

.mrg30 {
  margin-inline: 1.875rem;
}

.mrg35 {
  margin-inline: 2.1875rem;
}

.mrg40 {
  margin-inline: 2.5rem;
}

.mrg45 {
  margin-inline: 2.8125rem;
}

.mrg50 {
  margin-inline: 3.125rem;
}

/* ------------------- */
/* position            */
/* ------------------- */

.relative {
  position: relative;
}

.absolute {
  position: absolute;
}

.fixed {
  position: fixed;
}

.pointer {
  cursor: pointer;
}
